
'use client';

import { useState, useEffect, useMemo } from 'react';
import { collection, query, where, getDocs, QueryConstraint } from 'firebase/firestore';
import { db } from '@/config/firebase'; // Import Firestore instance
import CaseCard from '@/components/cases/CaseCard';
import type { Case } from '@/types';
// import { mockCases } from '@/data/mockData'; // Keep for potential fallback or testing
import { useAuth } from '@/contexts/AuthContext';
import { Button } from '@/components/ui/button';
import Link from 'next/link';
import { AlertTriangle, ListFilter, Loader2, PlusCircle, SearchX, LayoutDashboard } from 'lucide-react';
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"
import { Input } from '@/components/ui/input'; // Import Input for search
import { useTranslation } from 'react-i18next';

export default function DashboardPage() {
  const { user, loading: authLoading } = useAuth();
  const [allCases, setAllCases] = useState<Case[]>([]);
  const [filteredCases, setFilteredCases] = useState<Case[]>([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [statusFilter, setStatusFilter] = useState<string>('all');
  const [isLoading, setIsLoading] = useState(true);
  const { t } = useTranslation();

  useEffect(() => {
    const fetchCases = async () => {
      if (!user) {
        setIsLoading(false);
        return;
      }; // Don't fetch if user is not logged in

      setIsLoading(true);
      const casesRef = collection(db, 'cases');
      let qConstraints: QueryConstraint[] = [];

      // Build query based on user role
      switch (user.role) {
        case 'Judge':
          qConstraints.push(where('judgeId', '==', user.id));
          break;
        case 'Plaintiff':
          qConstraints.push(where('plaintiffId', '==', user.id));
          break;
        case 'Defendant':
          qConstraints.push(where('defendantId', '==', user.id));
          break;
        case 'Lawyer':
          // Firestore limitation: Cannot query array directly with '==' for multiple values.
          // Fetch all cases and filter client-side OR restructure data (e.g., lawyer subcollection).
          // For simplicity, fetching all and filtering client-side here. Consider performance for large datasets.
          // Alternatively, fetch cases where lawyerIds array-contains user.id
           qConstraints.push(where('lawyerIds', 'array-contains', user.id));
          break;
        case 'CourtOfficial':
          // Court Officials can see all cases - no specific user-based constraint needed here.
          // Access is controlled by Firestore rules.
          break;
        default:
          // Unknown role or role without specific case view, show nothing
          console.warn(`Unknown or unsupported role for case fetching: ${user.role}`);
           setAllCases([]);
           setFilteredCases([]);
           setIsLoading(false);
           return;
      }

      // Apply status filter if not 'all'
      if (statusFilter !== 'all') {
        qConstraints.push(where('status', '==', statusFilter));
      }

      // Construct the final query
      const q = query(casesRef, ...qConstraints);

      try {
        const querySnapshot = await getDocs(q);
        const casesData = querySnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() } as Case));

        // Apply search term filtering client-side after fetching
        let finalFilteredCases = casesData;
        if (searchTerm) {
          const lowerSearchTerm = searchTerm.toLowerCase();
          finalFilteredCases = casesData.filter(c =>
            c.caseNumber.toLowerCase().includes(lowerSearchTerm) ||
            c.title.toLowerCase().includes(lowerSearchTerm) ||
            (c.plaintiff && c.plaintiff.toLowerCase().includes(lowerSearchTerm)) ||
            (c.defendant && c.defendant.toLowerCase().includes(lowerSearchTerm))
          );
        }

         setAllCases(casesData); // Store the full fetched list (based on role constraints)
         setFilteredCases(finalFilteredCases); // Set the filtered list for display

      } catch (error) {
        console.error("Error fetching cases: ", error);
        // Handle error appropriately, maybe show a toast message
      } finally {
        setIsLoading(false);
      }
    };

    if (!authLoading && user) {
      fetchCases();
    } else if (!authLoading && !user) {
        setIsLoading(false); // Stop loading if not authenticated
        setAllCases([]);
        setFilteredCases([]);
    }
  }, [user, authLoading, statusFilter, searchTerm]); // Re-fetch when user, auth state, or filters change

  const handleSearch = (event: React.ChangeEvent<HTMLInputElement>) => {
    setSearchTerm(event.target.value);
  };


  const uniqueStatuses = useMemo(() => {
    // Calculate unique statuses from the currently fetched cases (respecting role filters)
    const statuses = new Set(allCases.map(c => c.status));
    return ['all', ...Array.from(statuses).sort()];
  }, [allCases]);


  if (authLoading || isLoading) {
    return (
      <div className="flex justify-center items-center min-h-[calc(100vh-200px)]">
        <Loader2 className="h-12 w-12 animate-spin text-primary" />
        <p className="ml-4 text-lg text-muted-foreground">{t('dashboard.loading')}</p>
      </div>
    );
  }

  if (!user) {
    return (
      <div className="text-center py-10">
        <AlertTriangle className="mx-auto h-12 w-12 text-destructive mb-4" />
        <h1 className="text-2xl font-semibold mb-2">{t('toast.accessDenied.title')}</h1>
        <p className="text-muted-foreground mb-6">{t('accessDenied.loginRequired.page', { pageName: t('header.dashboard')})}</p>
        <Link href="/login">
          <Button>{t('login.loginButton')}</Button>
        </Link>
      </div>
    );
  }

  // Determine dashboard title based on role
  let dashboardTitleKey = 'dashboard.title.other';
  if (user.role === 'Judge') dashboardTitleKey = 'dashboard.title.judge';
  // Add more specific titles if needed for other roles

  return (
    <div className="container mx-auto py-8 px-4 md:px-6">
      <div className="flex flex-col md:flex-row justify-between items-center mb-8 gap-4">
        <h1 className="text-3xl font-bold tracking-tight flex items-center">
          <LayoutDashboard className="mr-3 h-8 w-8 text-primary" />
          {t(dashboardTitleKey)}
        </h1>
        {/* Allow Court Officials to add cases */}
        {user.role === 'CourtOfficial' && (
           <Link href="/cases/new">
            <Button>
              <PlusCircle className="mr-2 h-4 w-4" /> {t('dashboard.addNewCase')}
            </Button>
          </Link>
        )}
      </div>

       {/* Search and Filter Controls */}
      <div className="flex flex-col md:flex-row gap-4 mb-6 p-4 bg-muted/50 rounded-lg border">
         <div className="relative flex-grow">
             <Input
               type="search"
               placeholder={t('caseSearch.placeholder')}
               value={searchTerm}
               onChange={handleSearch}
               className="pl-10 w-full bg-background" // Ensure input takes full width on small screens
               aria-label={t('caseSearch.placeholder')}
             />
             <SearchX className="absolute left-3 top-1/2 -translate-y-1/2 h-5 w-5 text-muted-foreground" />
           </div>
        <div className="flex items-center gap-2 w-full md:w-auto"> {/* Allow filter to take full width on small screens */}
          <ListFilter className="h-5 w-5 text-muted-foreground flex-shrink-0" />
          <Select value={statusFilter} onValueChange={setStatusFilter}>
            <SelectTrigger className="w-full md:w-[200px] bg-background" aria-label={t('filterByStatus')}>
              <SelectValue placeholder={t('filterByStatus')} />
            </SelectTrigger>
            <SelectContent>
              {uniqueStatuses.map(status => (
                <SelectItem key={status} value={status}>
                  {status === 'all' ? t('allStatuses') : t(`status.${status}`)}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>
      </div>

      {/* Case Grid */}
      {filteredCases.length > 0 ? (
        <div className="grid grid-cols-1 lg:grid-cols-2 xl:grid-cols-3 gap-6">
          {filteredCases.map((caseItem) => (
            <CaseCard key={caseItem.id} caseData={caseItem} />
          ))}
        </div>
      ) : (
         // No Cases Found Message
        <div className="text-center py-16 bg-card rounded-lg border-2 border-dashed">
          <SearchX className="mx-auto h-20 w-20 text-muted-foreground mb-6" />
          <h2 className="text-2xl font-semibold text-foreground">{t('dashboard.noCasesFound')}</h2>
          <p className="text-muted-foreground mt-2 max-w-md mx-auto">
            {searchTerm || statusFilter !== 'all'
              ? t('dashboard.noCasesFound.description.filtered')
              : (user.role === 'Judge' ? t('dashboard.noCasesFound.description.judge.empty') : t('dashboard.noCasesFound.description.other.empty'))
            }
          </p>
          {user.role === 'CourtOfficial' && !searchTerm && statusFilter === 'all' && allCases.length === 0 && (
            <Link href="/cases/new" className="mt-6 inline-block">
              <Button variant="outline">
                <PlusCircle className="mr-2 h-4 w-4" /> {t('dashboard.addYourFirstCase')}
              </Button>
            </Link>
          )}
        </div>
      )}
    </div>
  );
}
